    import {mapState, mapGetters} from 'vuex';

    export default {
		name: 'app-product-list',
	    data() {
            return {
                is_vip: true,
				disable: 'disable'
            }
	    },
        computed: {
            ...mapState({
                appImg: state => state.mallConfig.__wxapp_img.mall,
				appSetting: state => state.mallConfig.mall.setting,
                platform: function(state) {
                    return state.gConfig.systemInfo.platform;
                }
            }),
			...mapGetters('mallConfig', {
				getVideo: 'getVideo'
			}),
        },
	    props: {
            list: {
                type: Array,
	            default() {
                    return [];
	            }
            },
            status: {
                type: Number
            },
            empty: Boolean,
            botHeight: Number,
			theme: {
            	type: Object,
			},
	    },
	    methods: {
            route_go(data) {

				if (data.goods.video_url && this.getVideo == 1) {

					uni.navigateTo({
						url: `/pages/goods/video?goods_id=${data.goods_id}&sign=miaosha`
					});






				} else {
					uni.navigateTo({
						url: `/plugins/miaosha/goods/goods?id=${data.goods_id}`,
					});
				}







            }
	    }
    }
